:root {
  --clr-dark: #1a1f16;
  --clr-font: #1a1f16;
  --clr-font-grey: #60695c;
  --clr-background: #ededed;
  --clr-white: #fff;
  --clr-green: #12805d;
}

body {
  color: #1a1f16;
  font-family: Open Sans, sans-serif;
}

.master-container {
  color: var(--clr-font);
  background-color: #ededed;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2em;
}

aside {
  float: right;
  border: 1px dashed #00f;
  width: 25%;
  padding: 1em;
}

.aside__cart-wrapper {
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 1em;
  margin-top: 1em;
  display: flex;
}

.aside__cart_item {
  cursor: pointer;
  border-radius: 1em;
  width: 5em;
  display: block;
}

.aside__cart_item-img {
  width: 100%;
}

.aside__cart-btn {
  color: var(--clr-white);
  background-color: var(--clr-dark);
  cursor: pointer;
  border-radius: 1em;
  margin-inline: auto;
  padding: .5em 2em;
}

.aside__order-title {
  margin-top: 1em;
}

.aside__order-list {
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 1em;
  margin-top: 1em;
  display: flex;
}

.aside__order-number {
  cursor: pointer;
  border: none;
}

.product-list {
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 0;
  width: 75%;
  display: flex;
}

.product-item {
  border: 1px dashed red;
  width: 15em;
  padding: 1em;
}

.product-item__img {
  border-radius: 1.5em;
  width: 100%;
}

.product-item__content_desc {
  margin-top: 1em;
}

.product-item__content_part-number {
  color: var(--clr-font-grey);
  margin-top: .5em;
  font-size: .8em;
}

.product-item__content_price-block {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

